<template>
  <div class="container">
  <div id="server">
    <p class="title-txt">we're here to help ...</p>
    <div class="stage">
      
      <div class="curtain"><img class="img" src="../assets/stress.png" alt="stress"></div>
      <div class="event">
        <p class="event-txt">
          Life is hard, we know this
        </p>
      </div>
      
    </div>
    <div class="stage">
      <div class="event">
        <p class="event-txt" style="transition-delay: 2s;">
          Everyone needs a breather, from time to time
        </p>
      </div>
      <div class="curtain"><img class="img" src="../assets/runner.jpg" alt="runner"></div>
    </div>
    <div class="stage">
      <div class="curtain"><img class="img" src="../assets/zen.png" alt="zen"></div>
      <div class="event">
        <p class="event-txt" style="transition-delay: 4s;">
          That's what we promise
        </p>
      </div>

      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Description',
  mounted() {
    document.onscroll = function(){
      let rj = document.getElementById('server');
      if(document.documentElement.clientWidth < 768){
        if (window.pageYOffset > 450){
          console.log('hippie'); 
          let select = rj.querySelectorAll('.event-txt') ;
          select[0].classList.add('active');
          select[1].classList.add('active');
          select[2].classList.add('active');
        }
      }else if(document.documentElement.clientWidth > 760){
        if (window.pageYOffset > 600){
          console.log('hippie'); 
          let select = rj.querySelectorAll('.event-txt') ;
          select[0].classList.add('active');
          select[1].classList.add('active');
          select[1].classList.add('typewriter');
          select[2].classList.add('active');
        }
      }
  }
    }
}
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@500&display=swap');

  .container {
    display: flex;
    flex-flow: row nowrap;
    display: flex;
    justify-content: center;
    margin: 60px 40px;
  }
  .title-txt {
    font-family: 'Dosis', sans-serif;
    font-size: 20px;
    background: #333333;
    color: #ffffff;
    height: 8vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .stage {
    display: flex;
    flex-flow: row nowrap;
    margin: 70px 0px;
    justify-content: center;
  }
  .curtain {
    height: 40vh;
    width: 50%;
    padding: 10px;
  }
  .img {
      height: inherit;
      width: inherit;
      object-fit: contain;
  }
  .event {
    padding: 20px;
    display: flex;
    position: relative;
    align-content: center;
    justify-content: center;
    z-index: 2;
    background-color: #ffffff;
    flex-flow: column nowrap;
  }
  .event:after {
    content: "";
    display: block;
    margin: 0 auto;
    width: 40%;
    padding-top: 13px;
    border-bottom: 2px solid #5c0dda;
    z-index: 1;
  }
  .event-txt {
    font-size: 1.3em !important;
    color: #0e24e9;
    opacity: 0.1;
    transition: opacity ease-in 600ms; 
  }
  .active {
    opacity: 1;
  }

  .typewriter {
    overflow: hidden;
    border-right: .15em solid orange; /* The typwriter cursor */
    white-space: nowrap; /* Keeps the content on a single line */
    margin: 0 auto; /* Gives that scrolling effect as the typing happens */
    animation: 
      typing 4s steps(40, end),
      blink-caret .5s step-end infinite;
  }
  @keyframes typing {
  from { width: 0 }
	to { width: 60% }
  }
  @keyframes blink-caret {
  from, to { border-color: transparent }
	50% { border-color: orange }
  }

  @media(min-width: 700px) {
    .event-txt {font-size: 1rem;}
    .stage { margin: 0; }
    .curtain { height: 30vh; padding: 5px; }
  }
  @media(max-width: 500px) {
    .title-txt {
      font-size: 15px;
    }
    .stage { width: 100%; }
    .container { margin: 5px; }
    .event { padding: 5px; }
    .event-txt { font-size: 0.7em !important; }
    .curtain { height: 13vh; padding: 5px; }
  }
</style>