<template>
  <div class="cont">
    <p class="title-txt">testimonials ...</p>
    <div class="tes-cont">
      <div v-for="each in testimonials" :key="each" class="tes-details">
        <div class="tes-img-cont">
          <img :src= images[each.img] class="tes-img">
        </div>
        <div class="tes-txt">
          <p>{{ each.text }}</p>
          <p>- {{ each.name }}, {{ each.address }}</p>
        </div>
      </div>
    </div>

    <!--testimonials for mobile view -->
    <div class="mobile-tes-cont">
      <div class="mobile-tes-details">
        <div class="tes-txt">
          <p>{{ currentText }}</p>
          <p>- {{ currentName }}, {{ currentPlace }}</p>
        </div>
        <div class="btn-holder">
          <button class="switch-btn" @click="prev">&#10094;</button>
          <button class="switch-btn" @click="next">&#10095;</button>
        </div>
      </div>
      <div v-for="number in [currentNumber]" :key='number' class="mobile-tes-img-cont" :class="{ active: isActive }">
        <img :src="currentImage" class="tes-img">
      </div>
    </div>
    
  </div>
</template>

<script>
import testimonials from '../assets/Testimonials.js'
import joe from '../assets/prince-akachi-4Yv84VgQkRM-unsplash.jpg';
import mary from '../assets/terricks-noah-qT_VNmNAOCY-unsplash.jpg';
import remi from '../assets/darran-shen-Q_oXnBhxVu0-unsplash.jpg';
import musa from '../assets/anaya-katlego-tQ64LAblOUU-unsplash.jpg';

export default {
  name: 'testimonial',
  data() {
    return {
      images: [joe, mary, remi, musa],
      currentNumber: 0,
      isActive: false,
      testimonials
    }
  },
  methods: {
    next: function() {
      this.currentNumber += 1
      this.isActive = true
    },
    prev: function() {
      this.currentNumber -= 1
      this.isActive = true
    }
  },
  mounted() {
    this.showDivs(this.slideIndex);
  },
  computed: {
    currentImage: function() {
      return this.images[Math.abs(this.currentNumber) % this.images.length];
    },
    currentText: function() {
      return testimonials[Math.abs(this.currentNumber) % this.images.length].text;
    },
    currentName: function() {
      return testimonials[Math.abs(this.currentNumber) % this.images.length].name;
    },
    currentPlace: function() {
      return testimonials[Math.abs(this.currentNumber) % this.images.length].address;
    }
  }
}
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@500&display=swap');
  .cont {
    display: flex;
    flex-flow: column nowrap;
    margin: 60px 40px;
  }
  .title-txt {
    font-family: 'Dosis', sans-serif;
    font-size: 20px;
    color: #ffffff;
    margin: 60px 0;
    background: #333333;
    height: 8vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .tes-cont {
    display: flex;
    flex-flow: row nowrap;
  }
  .mobile-tes-cont {
    display: none;
    flex-flow: row-reverse nowrap;
    height: 40vh;
    margin: 0 10px;
    justify-content: center;
  }
  .tes-details {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;
  }
  .btn-holder {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
  }
  .tes-txt {
    width: 90%;
    margin: 5px;
    background-color: #0f0f0f;
    color:#ffffff;
    border: 1px solid #0f0f0f;
    padding: 5px;
    line-height: 1.3;
    font-family: sans-serif;
    font-size: 0.8em;
    height: 30vh;
    display: flex;
    flex-flow: column;
    justify-content: center;
  }
  .mobile-tes-txt {
    width: 50vw;
    margin: 0px 0px 5px 0px;
    background-color: #0f0f0f;
    color:#ffffff;
    border: 1px solid #0f0f0f;
    border-radius: 0px;
    padding: 5px;
    line-height: 1.3;
    font-family: sans-serif;
    height: 30vh;
    display: flex;
    flex-flow: column;
    justify-content: center;
  }
  .tes-img-cont {
    width: 90%;
    height: 30vh;
  }
  .mobile-tes-img-cont {
    height: 40vh;
    width: 30vw;
    padding-right: 5px;
    align-self: flex-end;
  }
  .tes-img {
    height: inherit;
    width: inherit;
  }
  .mobile-tes-details {
    margin: 0;
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-evenly;
    font-family: 'ZCOOL XiaoWei', serif;
    font-weight: 450;
    color: #0f0f0f;
  }
  .switch-btn {
    color: #ffffff;
    background-color: #0f0f0f;
    border: 1px solid #0f0f0f;
    height: 7vh;
    width: 4vw;
    margin: 0 15px;
    outline: none;
  }
  .switch-btn:hover {
    background-color: #e4e4e4;
    border: 1px solid #e4e4e4;
    color: #0f0f0f;
  }

  @media(max-width: 800px) {
    .tes-cont { display: none; }
    .mobile-tes-cont { display: flex; }
    .tes-details {
      font-size: 12px;
    }
    .cont {
      margin: 15px;
    }
    .tes-img-cont {
      width: 30vw;
    }
    .tes-img {
      height: inherit;
      width: inherit;
    }
  }
  @media(max-width: 500px) {
    .title-txt {
      font-size: 15px;
    }
    .tes-details {
      font-size: 10px;
    }
    .cont {
      margin: 10px;
    }
    .tes-cont {
      width: 100%;
    }
    .tes-img-cont {
      width: 30vw;
    }
    .tes-img {
      height: inherit;
      width: inherit;
    }
    .switch-btn { width: auto; height: 5vh; }
  }
</style>